body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.spinner-wrapper{
  padding-top: 150px;
}
.header {
    background-color: #081C24;
    color: #01d277;
}

.links {
    color: #01d277 !important;
}
.suggests-component {
    position: absolute;
    list-style: none;
    color: #FFFFFF;
    background-color: rgba(52, 58, 64, 0.7);
    width: 92%;
    top: 65px;
    text-align: left;
    cursor: pointer;
    border-radius: 4px;
    padding: 25px;
}
.suggests-component-item {
    padding: 5px 15px 5px 10px;
}

.suggests-component-item:hover {
    background-color: #01D277;
    border-radius: 5px;
}
.suggests-component a {
    text-decoration: none;
    color: #ffffff;
}
.suggests-component li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 2px solid grey;
}
.suggests-component h5 {
    display: inline-block;
    margin: 0;
}


.movieItem-wrapper {
    position: relative;
    width: 100%;
    padding: 120px 0 50px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position:  50% 0%;
    margin-bottom: 50px;
}

.overflow {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: -webkit-radial-gradient(20% 50%, circle, rgba(16.47%, 16.08%, 13.33%, 0.88) 0%, rgba(16.47%, 16.08%, 13.33%, 0.88) 100%);
    background-image: radial-gradient(circle at 20% 50%, rgba(16.47%, 16.08%, 13.33%, 0.88) 0%, rgba(16.47%, 16.08%, 13.33%, 0.88) 100%);
}

.testClass {
    text-transform: uppercase;
    padding-top: 5px;
    margin-right: 5px;
}

.film-info-wrapper {
    text-align: left;
    color: #FFFFFF;
}

.original-title {
    text-transform: uppercase;
    margin-bottom: 30px;
}

.tagline {
    display: block;
    font-size: 20px;
    margin-bottom: 10px;
    color: #01D277;
    letter-spacing: 2px;
    font-family: Roboto, sans-serif;
}

.overview h3{
    margin-top: 30px;
}

.overview p {
    margin-bottom: 30px;
}

.slider-wrapper {
    margin: 0 20px 50px;
    padding: 20px 0;
    text-align: left;
    width: 95%;
}

.slider-wrapper h4 {
    margin-bottom: 20px;
}
.related-movie-item-wrapper {
    margin: 0 5px;
}

.related-movie-item-wrapper img {
    width: 100%;
    object-fit: contain;
}

.related-movie-info {
    display: flex;
    justify-content: space-between;
}

.rating-wrapper {

}
.movie-card {
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    margin-bottom: 25px;
    overflow: hidden;
    text-align: left;
    font-size: 17px;
}
.card-title {
    font-size: 16px;
}
.poster-list {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
    object-fit: cover;
}

.poster-list img {
    height: 100%;
    width: 100%;

    object-fit: cover;
    /*object-position: 50% 100%;*/
    overflow: hidden;
}

.card-text {
    font-size: 14px;
    white-space: revert;
    width: 100%;
    height: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
}
